
html {
    font-family: "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
    width: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}

body {
    background-color: #000;
    width: 100%;
}

.wrap {
    width: 100%;
    overflow: hidden;
    padding-bottom: 24px;
    position: relative;
}

.share-ui {
    position: absolute;
    bottom: 24px;
    left: 50%;
    transform: translate3d(-50%,0,0);
    z-index: 50;
    &.is-relative {
        position: relative;
        bottom: inherit;
        left: inherit;
        transform: translate3d(0,0,0);
        width: 100%;
        margin: 0 auto;
        text-align: center;
    }
    @include susy-breakpoint($s-320, $c8) {
        bottom: 0;
    }
    &__btn-share {
        position: relative;
        cursor: pointer;
        display: inline-block;
        img {
            width: 42px;
            height: 42px;
            display: inline-block;
            vertical-align: middle;
            margin-right: 12px;
            @include susy-breakpoint($s-320, $c8) {
                width: 32px;
                height: 32px;
                margin-right: 6px;
            }
        }
        span {
            font-size: 30px;
            line-height: 40px;
            display: inline-block;
            vertical-align: middle;
            color: #fff;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 18px;
            }
        }
    }
    &__social-nav {
        font-size: 0;
        display: inline-block;
        position: absolute;
        background-color: #282828;
        border-radius: 8px;
        position: absolute;
        left: 50%;
        transform: translate3d(-50%,-80px,0);
        width: 140px;
        padding: 8px;
        text-align: center;
        opacity: 0;
        pointer-events: none;
        transition: all 0.5s ease-in-out;
        &.is-active {
            transform: translate3d(-50%,-60px,0);
            opacity: 1;
            pointer-events: auto;
        }
    }
    &__btn-fb,
    &__btn-line,
    &__btn-link {
        width: 36px;
        height: 36px;
        display: inline-block;
        margin: 0 4px;
        cursor: pointer;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
            pointer-events: none;
        }
    }
}

.modal {
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: rgba(0,0,0,0.95);
    left: 0;
    top: 0;
    z-index: 100;
    pointer-events: none;
    opacity: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease-in-out;
    transform: translate(0,-100%);
    &.is-active {
        pointer-events: auto;
        opacity: 1;
        transform: translate(0,0);
    }
    &__ui {
        background-color: #fff;
        width: 300px;
        box-sizing: border-box;
        border-radius: 16px;
        padding: 32px 16px;
        text-align: center;
        p {
            text-align: center;
            font-size: 20px;
            margin-bottom: 16px;
        }
    }
    &__btn-confirm {
        cursor: pointer;
        display: inline-block;
        text-decoration: none;
        margin: 5px;
        padding: 10px 30px;
        background: #5AC832;
        border-radius: 5px;
        font-size: 20px;
        color: #FFF;
        border: 0;
    }
}

.quick-menu {
    position: absolute;
    right: -10px;
    top: -120px;
    &--event {
        top: 0;
    }
    @include susy-breakpoint($s-768, $c12) {
        display: none;
    }
    a {
        width: 155px;
        height: 182px;
        display: block;
        position: absolute;
        img {
            width: 100%;
            display: block;
            margin: 0;
        }
        &:nth-child(1) {
            top: 0;
        }
        &:nth-child(2) {
            top: 140px;
        }
        &:nth-child(3) {
            top: 140px * 2;
        }
        &:nth-child(4) {
            top: 140px * 3;
        }
        &:nth-child(5) {
            top: 140px * 4;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 155px * 0.6;
            height: 182px * 0.6;
            &:nth-child(1) {
                top: 0;
            }
            &:nth-child(2) {
                top: 84px;
            }
            &:nth-child(3) {
                top: 84px * 2;
            }
            &:nth-child(4) {
                top: 84px * 3;
            }
            &:nth-child(5) {
                top: 84px * 4;
            }
        }
    }
}


.btn-back {
    width: 42px;
    height: 42px;
    display: block;
    position: absolute;
    right: 16px;
    bottom: 0;
    img {
        width: 100%;
        display: block;
        margin: 0;
    }
    @include susy-breakpoint($s-320, $c8) {
        width: 32px;
        height: 32px;
        bottom: 3px;
        right: 16px;
    }
}

.popup {
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    transition: all 0.5s ease-in-out;
    transform: translate3d(0,-100%,0);
    pointer-events: none;
    opacity: 0;
    &.is-active {
        pointer-events: auto;
        opacity: 1;
        transform: translate3d(0,0,0);
    }
    &__inner {
        max-width: 940px;
        width: 100%;
        height: 100%;
        margin: 0 auto;
        display: table;
        @include susy-breakpoint($s-320, $c8) {
            width: calc(100% - 32px);
            margin: 0 16px;
        }
    }
    &__video-inner {
        border: solid 1px #ab8d6c;
        width: 100%;
        padding-bottom: 56.25%;
        position: relative;
        iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
    &__video {
        display: table-cell;
        vertical-align: middle;
        width: 100%;
        height: 100%;
    }
    &__ui {
        width: 760px;
        padding: 48px;
        box-sizing: border-box;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%,-50%,0);
        background-color: rgba(0,0,0,0.8);
        border: solid 1px #5f4c3e;
        p {
            font-size: 24px;
            line-height: 1.6;
            color: #aa8968;
            text-align: center;
            margin-bottom: 24px;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: calc(100% - 32px);
            padding: 24px;
            p {
                font-size: 16px;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 700px;
        }
    }
    &__btn-confirm {
        padding: 0;
        color: #d49468;
        display: block;
        width: 219px;
        height: 75px;
        font-size: 24px;
        line-height: 60px;
        text-align: center;
        margin: 0 auto;
        font-weight: 700;
        transition: all 0.3s ease-in-out;
        border: 0;
        background: transparent url(../images/event-btn-1.png) no-repeat center top;
        &:hover {
            transform: translate3d(0,-4%,0);
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 219px * 0.6;
            height: 75px * 0.6;
            background-size: 219px * 0.6 auto;
            font-size: 16px;
            line-height: 60px * 0.6;
        }
    }
}
