.section-event {
    width: 100%;
    background: #000 url(../images/event-bg.jpg) no-repeat center top;
    @include susy-breakpoint($s-320, $c8) {
        background-image: url(../images/event-bg-m.jpg);
        background-size: 100% auto;
    }
    &__particle {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 0;
        left: 0;
        top: 0;
        overflow: hidden;
    }
    &__inner {
        position: relative;
        width: 1200px;
        margin: 0 auto;
        z-index: 10;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 720px;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 800px;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 900px;
        }
    }
    &__logo {
        position: absolute;
        display: block;
        left: 30px;
        top: 30px;
        z-index: 120;
        img {
            height: 70px;
        }
        @include susy-breakpoint($s-320, $c8) {
            left: 50%;
            top: 12px;
            transform: translate3d(-50%,0,0);
            img {
                height: 48px;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            left: 0;
            img {
                height: 56px;
            }
        }
    }
    &__title {
        text-align: center;
        padding-top: 80px;
        margin-bottom: 24px;
        img {
            height: 56px;
        }
        @include susy-breakpoint($s-320, $c8) {
            padding-top: 72px;
            img {
                height: 32px;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            img {
                height: 48px;
            }
        }
    }
    &__nav {
        width: 750px;
        height: 190px;
        position: relative;
        margin: 0 auto 16px;
        @include susy-breakpoint($s-320, $c8) {
            width: 300px;
            height: 140px;
            margin-bottom: 0;
        }
    }
    &__btn {
        display: block;
        width: 154px;
        height: 130px;
        background: transparent url(../images/event-nav-bg.png) no-repeat 0 0;
        position: absolute;
        font-size: 0;
        left: 50%;
        top: 50%;
        -webkit-tap-highlight-color: rgba(0,0,0,0);
        @include susy-breakpoint($s-320, $c8) {
            width: 90px;
            height: 76px;
            background-size: 180px auto;
        }
        &--1 {
            transform: translate3d(-243%,-73%,0);
            background-position: 0 0;
            &:hover {
                background-position: -154px 0;
            }
            &.is-active {
                background-position: -154px 0;
            }
            @include susy-breakpoint($s-320, $c8) {
                transform: translate3d(-205%,-93%,0);
                &:hover {
                    background-position: 0 0;
                }
                &.is-active {
                    background-position: -90px 0;
                }
            }
        }
        &--2 {
            transform: translate3d(-167%,-22%,0);
            background-position: 0 -130px;
            &:hover {
                background-position: -154px -130px;
            }
            &.is-active {
                background-position: -154px -130px;
            }
            @include susy-breakpoint($s-320, $c8) {
                transform: translate3d(-142%,-22%,0);
                background-position: 0 -76px;
                &:hover {
                    background-position: 0 -76px;
                }
                &.is-active {
                    background-position: -90px -76px;
                }
            }
        }
        &--3 {
            transform: translate3d(-90%,-73%,0);
            background-position: 0 -260px;
            &:hover {
                background-position: -154px -260px;
            }
            &.is-active {
                background-position: -154px -260px;
            }
            @include susy-breakpoint($s-320, $c8) {
                transform: translate3d(-80%,-93%,0);
                background-position: 0 -152px;
                &:hover {
                    background-position: 0 -152px;
                }
                &.is-active {
                    background-position: -90px -152px;
                }
            }
        }
        &--4 {
            transform: translate3d(-14%,-22%,0);
            background-position: 0 -390px;
            &:hover {
                background-position: -154px -390px;
            }
            &.is-active {
                background-position: -154px -390px;
            }
            @include susy-breakpoint($s-320, $c8) {
                transform: translate3d(-18%,-22%,0);
                background-position: 0 -228px;
                &:hover {
                    background-position: 0 -228px;
                }
                &.is-active {
                    background-position: -90px -228px;
                }
            }
        }
        &--5 {
            transform: translate3d(62%,-73%,0);
            background-position: 0 -520px;
            &:hover {
                background-position: -154px -520px;
            }
            &.is-active {
                background-position: -154px -520px;
            }
            @include susy-breakpoint($s-320, $c8) {
                transform: translate3d(43%,-93%,0);
                background-position: 0 -304px;
                &:hover {
                    background-position: -0 -304px;
                }
                &.is-active {
                    background-position: -90px -304px;
                }
            }
        }
        &--6 {
            transform: translate3d(138%,-22%,0);
            background-position: 0 -650px;
            &:hover {
                background-position: -154px -650px;
            }
            @include susy-breakpoint($s-320, $c8) {
                transform: translate3d(105%,-22%,0);
                background-position: 0 -380px;
                &:hover {
                    background-position: 0 -380px;
                }
            }
        }
    }
    &__container {
        position: relative;
        box-sizing: border-box;
        width: calc(1200px - 32px);
        margin: 0 auto 32px;
        padding: 64px 148px;
        border: solid 1px #a88969;
        background-color: rgba(0,0,0,.5);
        @include susy-breakpoint($s-320, $c8) {
            width: calc(100% - 20px);
            padding: 16px;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: calc(720px - 32px);
            padding: 24px;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 800px;
            padding-left: 64px;
            padding-right: 64px;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 900px;
            padding-left: 108px;
            padding-right: 108px;
        }
    }
}

.event-item {
    margin: 0 auto;
    font-family: 'Noto Serif TC', serif;
    &__h3 {
        font-size: 30px;
        color: #c6a17a;
        line-height: 1.6;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 18px;
        }
    }
    &__h4 {
        font-size: 18px;
        color: #c6a17a;
        line-height: 1.6;
        margin-bottom: 8px;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 15px;
            margin-bottom: 4px;
        }
        strong {
            color: #ed1f1f;
            padding-left: 16px;
        }
    }
    &__p {
        color: #c6a17a;
        font-size: 20px;
        line-height: 1.6;
        margin-bottom: 48px;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 14px;
            margin-bottom: 24px;
        }
        &--lv2 {
            font-size: 18px;
            color: #debf9f;
            margin-bottom: 8px;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 14px;
                margin-bottom: 4px;
            }
        }
        &--highlight {
            color: #ffe7fd;
            text-align: center;
            br {
                display: none;
            }
            @include susy-breakpoint($s-320, $c8) {
                strong {
                    display: none;
                }
                br {
                    display: block;
                }
            }
        }
    }
    &__method-list {
        counter-reset: method-list;
        margin: 0;
        padding: 0;
        & > li {
            font-size: 18px;
            color: #c6a17a;
            line-height: 1.6;
            padding-left: 24px;
            position: relative;
            counter-increment: method-list;
            &::before {
                content: counter(method-list) ".";
                position: absolute;
                font-size: 18px;
                color: #c6a17a;
                top: 0;
                left: 0;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 14px;
                &::before {
                    font-size: 14px;
                }
            }
        }
        strong {
            color: #ed1f1f;
        }
    }
    &__method-list-inner {
        margin: 0;
        padding: 0;
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 0;
            margin-bottom: 16px;
        }
        li {
            font-size: 18px;
            color: #debf9f;
            line-height: 1.6;
            padding-left: 24px;
            position: relative;
            &::before {
                content: "§";
                position: absolute;
                font-size: 18px;
                color: #debf9f;
                top: 0;
                left: 0;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 14px;
                padding-left: 16px;
                &::before {
                    font-size: 14px;
                }
            }
        }
    }
    &__event-list {
        margin: 0 0 32px;
        padding: 0 0 0 16px;
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 0;
            margin-bottom: 16px;
        }
        li {
            font-size: 18px;
            color: #debf9f;
            line-height: 1.6;
            padding-left: 24px;
            position: relative;
            &::before {
                content: "★";
                position: absolute;
                font-size: 18px;
                color: #debf9f;
                top: 0;
                left: 0;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 14px;
                padding-left: 16px;
                &::before {
                    font-size: 14px;
                }
            }
            a {
                color: #ffbd3d;
            }
        }
    }
    &__section {
        margin-bottom: 48px;
        transform: translate3d(0,-20px,0);
        transition: all 0.6s ease-in-out 0.5s;
        opacity: 0;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 24px;
        }
        &.is-show {
            transform: translate3d(0,0,0);
            opacity: 1;
        }
    }
    &__prize-wrap {
        text-align: center;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 24px;
        }
    }
    &__prize {
        font-size: 0;
        margin-bottom: 24px;
        &--col {
            display: inline-block;
            width: calc(50% - 16px);
            @include susy-breakpoint($s-320, $c8) {
                width: 100%;
            }
            @include susy-breakpoint($s-768, $c12) {
                width: 100%;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 100%;
            }
        }
        &:last-child {
            margin-bottom: 0;
        }
        img {
            display: inline-block;
            width: 210px;
            vertical-align: top;
            @include susy-breakpoint($s-320, $c8) {
                width: 100px;
                margin-top: 20px;
            }
        }
        
    }
    &__prize-info {
        display: inline-block;
        width: calc(100% - 210px);
        vertical-align: top;
        padding-top: 32px;
        text-align: left;
        @include susy-breakpoint($s-320, $c8) {
            width: calc(100% - 108px);
            padding-top: 16px;
        }
        h5 {
            font-size: 20px;
            line-height: 1.6;
            color: #c6a17a;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 15px;
            }
        }
        p {
            color: #debf9f;
            font-size: 18px;
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 14px;
            }
        }
        strong {
            color: #ed1f1f;
            font-size: 18px;
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 14px;
            }
        }
    }
    &__remind-text {
        position: relative;
        text-align: center;
        width: 100%;
        display: table;
        white-space: nowrap;
        font-size: 18px;
        color: #c6a17a;
        line-height: 1.6;
        vertical-align: middle;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 15px;
        }
        &::before {
            border-top: 1px solid #c6a17a;
            content: '';
            display: table-cell;
            position: relative;
            top: 1.2em;
            width: 45%;
            right: 2%;
        }
        &::after {
            border-top: 1px solid #c6a17a;
            content: '';
            display: table-cell;
            position: relative;
            top: 1.2em;
            width: 45%;
            left: 2%;
        }
        &--single {
            &::before {
                top: 0.8em;
            }
            &::after {
                top: 0.8em;
            }
        }
    }
    &__text {
        display: inline-block;
        vertical-align: top;
        width: 60%;
        opacity: 0;
        transform: translate3d(0,-20px,0);
        transition: all 0.6s ease-in-out;
        img {
            max-width: 100%;
        }
        &.is-show {
            transform: translate3d(0,0,0);
            opacity: 1;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            margin-bottom: 32px;
        }
    }
    &__item {
        display: inline-block;
        vertical-align: top;
        width: 40%;
        opacity: 0;
        transform: translate3d(0,-20px,0);
        transition: all 0.6s ease-in-out 0.3s;
        img {
            max-width: 100%;
        }
        &.is-show {
            transform: translate3d(0,0,0);
            opacity: 1;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            margin-bottom: 32px;
        }
    }
    &__link {
        text-align: center;
        opacity: 0;
        transform: translate3d(0,-20px,0);
        transition: all 0.6s ease-in-out 0.5s;
        &.is-show {
            transform: translate3d(0,0,0);
            opacity: 1;
        }
    }
    &__btn-more,
    &__btn-account {
        display: inline-block;
        width: 219px;
        height: 75px;
        font-size: 24px;
        line-height: 66px;
        text-align: center;
        margin: 0 16px;
        font-weight: 700;
        transition: all 0.3s ease-in-out;
        &:hover {
            transform: translate3d(0,-4%,0);
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 130px;
            height: 45px;
            line-height: 40px;
            font-size: 14px;
            margin: 0 4px;
        }
    }
    &__btn-more {
        background: transparent url(../images/event-btn-1.png) no-repeat center top;
        color: #fff;
        text-shadow: 0px -1px 0px rgba(6, 0, 1, 0.75);
        @include susy-breakpoint($s-320, $c8) {
            background-size: 130px 45px;
        }
    }
    &__btn-account {
        display: block;
        margin: 0 auto;
        color: #fff;
        text-shadow: 0px -1px 0px rgba(6, 0, 1, 0.75);
        background: transparent url(../images/event-btn-2.png) no-repeat center top;
        @include susy-breakpoint($s-320, $c8) {
            background-size: 130px 45px;
        }
    }
    &__dl {
        margin-bottom: 48px;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 24px;
        }
        dt {
            font-size: 18px;
            color: #debf9f;
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 15px;
            }
        }
        dd {
            font-size: 18px;
            color: #debf9f;
            line-height: 1.6;
            padding-left: 36px;
            margin-bottom: 16px;
            &:last-child {
                margin-bottom: 0;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 14px;
                padding-left: 32px;
            }
        }
    }
    &__set {
        margin-bottom: 48px;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 24px;
        }
        h5 {
            font-size: 18px;
            color: #c6a17a;
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 15px;
            }
        }
    }
    &__text-info {
        position: relative;
        color: #debf9f;
        line-height: 1.6;
        padding-left: 72px;
        font-size: 18px;
        margin-bottom: 16px;
        &::before {
            position: absolute;
            left: 20px;
            top: 0;
            content: "說明：";
            font-size: 18px;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 14px;
            padding-left: 62px;
            &::before {
                font-size: 14px;
            }
        }
    }
    &__text-prize {
        position: relative;
        color: #edac1f;
        line-height: 1.6;
        padding-left: 72px;
        font-size: 18px;
        margin-bottom: 16px;
        &::before {
            position: absolute;
            left: 20px;
            top: 0;
            content: "說明：";
            font-size: 18px;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 14px;
            padding-left: 62px;
            &::before {
                font-size: 14px;
            }
        }
    }
}

.event-rule {
    width: 1100px;
    font-family: 'Noto Serif TC', serif;
    margin-bottom: 64px;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
    @include susy-breakpoint($s-320, $c8) {
        padding: 0 16px;
        margin-bottom: 32px;
        width: 100%;
    }
    @include susy-breakpoint($s-768, $c12) {
        width: 720px;
    }
    @include susy-breakpoint($s-960, $c12) {
        width: 940px;
    }
    &__btn-toggle {
        height: 40px;
        font-size: 18px;
        color: #c6a17a;
        position: relative;
        background-color: transparent;
        border: 0;
        line-height: 40px;
        padding: 0;
        margin-bottom: 24px;
        &::before {
            width: 40px;
            height: 40px;
            background: transparent url(../images/icon-open@2x.png) no-repeat 0 0;
            background-size: 40px;
            position: absolute;
            right: -48px;
            top: 0;
            content: "";
            transition: opacity 0.6s ease-in-out;
            @include susy-breakpoint($s-320, $c8) {
                width: 32px;
                height: 32px;
                background-size: 32px;
                top: 4px;
                right: -40px;
            }
        }
        &::after {
            width: 40px;
            height: 40px;
            background: transparent url(../images/icon-close@2x.png) no-repeat 0 0;
            background-size: 40px;
            position: absolute;
            right: -48px;
            top: 0;
            content: "";
            opacity: 0;
            transition: opacity 0.6s ease-in-out;
            @include susy-breakpoint($s-320, $c8) {
                width: 32px;
                height: 32px;
                background-size: 32px;
                top: 4px;
                right: -40px;
            }
        }
        &.is-active {
            &::before {
                opacity: 0;
            }
            &::after {
                opacity: 1;
            }
        }
    }
    &__rule-list {
        padding: 0;
        margin: 0;
        li {
            position: relative;
            font-size: 18px;
            color: #debf9f;
            line-height: 1.6;
            margin-bottom: 8px;
            padding-left: 18px;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 14px;
            }
            &:last-child {
                margin-bottom: 0;
            }
            &::before {
                position: absolute;
                top: 14px;
                left: 4px;
                content: "";
                background-color: #b69c82;
                width: 8px;
                height: 1px;
                @include susy-breakpoint($s-320, $c8) {
                    top: 12px
                }
            }
        }
        a {
            color: #ffbd3d;
            transition: color 0.4s ease-in-out;
            &:hover {
                color: #fff;
            }
        }
    }
}