
.section-home {
    width: 100%;
    background: #000 url(../images/home-bg-3.jpg) no-repeat center top;
    background-size: cover;
    height: 1100px;
    position: relative;
    @include susy-breakpoint($s-320, $c8) {
        background-image: url(../images/home-bg-m-4.jpg);
        background-position: center center;
        background-size: cover;
        height: 680px;
    }
    @include susy-breakpoint($s-768, $c12) {
        height: 700px;
    }
    @include susy-breakpoint($s-960, $c12) {
        height: 800px;
    }
    @include susy-breakpoint($s-1201, $c18) {
        height: 920px;
    }
    &::after {
        width: 100%;
        height: 300px;
        position: absolute;
        bottom: 0;
        left: 0;
        background: rgb(0,0,0);
        background: linear-gradient(0deg, rgba(0,0,0,1) 12%, rgba(0,0,0,0) 94%);
        z-index: 0;
        content: "";
    }
    &__inner {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%,-50%,0);
        width: 1200px;
        height: 100%;
        z-index: 10;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            top: 0;
            transform: translate3d(-50%,0,0);
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 720px;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 920px;
        }
    }
    &__logo {
        text-align: center;
        display: block;
        position: absolute;
        top: 24px;
        left: 24px;
        z-index: 100;
        img {
            height: 72px;
        }
        @include susy-breakpoint($s-320, $c8) {
            top: 12px;
            left: 12px;
            img {
                height: 54px;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            img {
                height: 72px * 0.7;
            }
        }
        @include susy-breakpoint($s-960, $c12) {
            img {
                height: 72px * 0.7;
            }
        }
        @include susy-breakpoint($s-1201, $c18) {
            img {
                height: 72px * 0.8;
            }
        }
    }
    &__main-title {
        text-align: center;
        padding-top: 360px;
        position: relative;
        z-index: 0;
        margin-bottom: 8px;
        img {
            height: 312px;
        }
        @include susy-breakpoint($s-320, $c8) {
            position: relative;
            height: 140px;
            padding-top: 232px;
            margin-bottom: 24px;
            img {
                height: 140px;
                position: absolute;
                left: 50%;
                transform: translate3d(-50%,0,0);
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            padding-top: 220px;
            margin-bottom: 0;
            img {
                height: 312px * 0.7;
            }
        }
        @include susy-breakpoint($s-960, $c12) {
            padding-top: 220px;
            margin-bottom: 0;
            img {
                height: 312px * 0.7;
            }
        }
        @include susy-breakpoint($s-1201, $c18) {
            padding-top: 260px;
            margin-bottom: 0;
            img {
                height: 312px * 0.9;
            }
        }
    }
    &__nav {
        text-align: center;
        position: relative;
        z-index: 10;
        @include susy-breakpoint($s-320, $c8) {
            width: 300px;
            margin: 0 auto;
        }
    }
    &__btn-event,
    &__btn-intro,
    &__btn-download,
    &__btn-account {
        width: 225px;
        height: 201px;
        display: inline-block;
        position: relative;
        margin: 0 16px;
        img {
            width: 100%;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 225px * 0.5;
            height: 201px * 0.5;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 225px * 0.6;
            height: 201px * 0.6;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 225px * 0.7;
            height: 201px * 0.7;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 225px * 0.8;
            height: 201px * 0.8;
            margin: 0 12px;
        }
        .front {
            position: absolute;
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            display: block;
            transform: rotateY(0deg);
            opacity: 0;
            animation: flip_back1 0.3s 1 linear forwards;
        }
        .back {
            position: absolute;
            width: 100%;
            height: 100%;
            display: block;
            opacity: 0;
            animation: flip_back2 0.3s 1 linear forwards;
        }
        &:hover {
            .front {
                animation: flip_foward1 0.3s 1 linear forwards;
            }
            .back {
                animation: flip_foward2 0.3s 1 linear forwards;
            }
            @include susy-breakpoint($s-320, $c8) {
                .front {
                    opacity: 1;
                }
                .back {
                    opacity: 0;
                }
            }
        }
    }
}
@keyframes flip_back1 {
    0% {
        opacity: 0
    }

    50% {
        opacity: 0
    }

    51% {
        -webkit-transform: rotateY(90deg);
        transform: rotateY(90deg);
        opacity: 1
    }

    100% {
        -webkit-transform: rotateY(0deg);
        transform: rotateY(0deg);
        opacity: 1
    }
}

@keyframes flip_back2 {
    0% {
        -webkit-transform: rotateY(360deg) scale(1.05);
        transform: rotateY(360deg) scale(1.05);
        opacity: 1
    }

    50% {
        -webkit-transform: rotateY(270deg) scale(1.05);
        transform: rotateY(270deg) scale(1.05);
        opacity: 1
    }

    51% {
        opacity: 0;
        -webkit-transform: scale(1.05);
        transform: scale(1.15)
    }

    100% {
        opacity: 0
    }
}

@keyframes flip_foward1 {
    0% {
        -webkit-transform: rotateY(0deg);
        transform: rotateY(0deg);
        opacity: 1
    }

    50% {
        -webkit-transform: rotateY(90deg);
        transform: rotateY(90deg);
        opacity: 1
    }

    51% {
        opacity: 0
    }

    100% {
        opacity: 0
    }
}

@keyframes flip_foward2 {
    0% {
        opacity: 0
    }

    50% {
        opacity: 0;
        -webkit-transform: scale(1.1);
        transform: scale(1.1)
    }

    51% {
        -webkit-transform: rotateY(270deg) scale(1.05);
        transform: rotateY(270deg) scale(1.05);
        opacity: 1
    }

    100% {
        -webkit-transform: rotateY(360deg) scale(1.1);
        transform: rotateY(360deg) scale(1.1);
        opacity: 1
    }
}