.inf-theme {
    width: 100%;
    height: 100%;
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
    &::after {
        width: 100%;
        height: 300px;
        position: absolute;
        bottom: 0;
        left: 0;
        background: rgb(0,0,0);
        background: linear-gradient(0deg, rgba(0,0,0,1) 12%, rgba(0,0,0,0) 94%);
        z-index: 80;
        content: "";
    }
    &__theme {
        width: 100%;
        height: 100%;
        z-index: 0;
        position: absolute;
        opacity: 0;
        top: 0;
        left: 0;
        transition: opacity 1s ease;
        z-index: 0;
        pointer-events: none;
        &--1 {
            background: #000 url(../images/inf-theme-1.jpg) no-repeat center top;
        }
        &--2 {
            background: #000 url(../images/inf-theme-2.jpg) no-repeat center top;
        }
        &--3 {
            background: #000 url(../images/inf-theme-3.jpg) no-repeat center top;
        }
        &--4 {
            background: #000 url(../images/inf-theme-4.jpg) no-repeat center top;
        }
        &.is-active {
            opacity: 1;
        }
        @include susy-breakpoint($s-320, $c8) {
            &--1 {
                background-image: url(../images/inf-theme-1-m.jpg);
                background-size: 100% auto;
                background-repeat: no-repeat;
            }
            &--2 {
                background-image: url(../images/inf-theme-2-m.jpg);
                background-size: 100% auto;
                background-repeat: no-repeat;
            }
            &--3 {
                background-image: url(../images/inf-theme-3-m.jpg);
                background-size: 100% auto;
                background-repeat: no-repeat;
            }
            &--4 {
                background-image: url(../images/inf-theme-4-m.jpg);
                background-size: 100% auto;
                background-repeat: no-repeat;
            }
        }
    }
}
.section-inf {
    width: 100%;
    position: relative;
    z-index: 10;
    &__inner {
        position: relative;
        width: 1200px;
        margin: 0 auto;
        z-index: 10;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 720px;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 800px;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 900px;
        }
    }
    &__logo {
        position: absolute;
        display: block;
        left: 30px;
        top: 30px;
        z-index: 120;
        img {
            height: 70px;
        }
        @include susy-breakpoint($s-320, $c8) {
            left: 50%;
            top: 12px;
            transform: translate3d(-50%,0,0);
            img {
                height: 48px;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            left: 0;
            img {
                height: 56px;
            }
        }
    }
    &__title {
        text-align: center;
        padding-top: 80px;
        margin-bottom: 24px;
        img {
            height: 56px;
        }
        @include susy-breakpoint($s-320, $c8) {
            padding-top: 72px;
            img {
                height: 32px;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            img {
                height: 48px;
            }
        }
    }
    &__nav {
        width: 1080px;
        position: relative;
        margin: 0 auto 64px;
        padding-top: 160px;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            background-color: rgba(0,0,0,0.9);
            position: fixed;
            top: 0;
            left: 0;
            height: 100vh;
            z-index: 30;
            transform: translate3d(100%,0,0);
            transition: all 0.6s ease-in-out;
            padding-top: 128px;
            box-sizing: border-box;
            opacity: 0;
            &.is-active {
                transform: translate3d(0,0,0);
                opacity: 1;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 720px;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 800px;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 800px;
        }
    }
    &__btn {
        box-sizing: border-box;
        font-family: 'Noto Sans TC', sans-serif;
        display: inline-block;
        width: calc(20% - 4px);
        height: 48px;
        line-height: 46px;
        font-size: 24px;
        color: #a88969;
        text-align: center;
        -webkit-tap-highlight-color: rgba(0,0,0,0);
        position: relative;
        text-shadow: 0 0 0 rgba(255,255,255,0);
        transition: all 0.3s ease-in-out;
        &:after {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            background: transparent url(../images/inf-nav-bg@2x.png) no-repeat 0 0;
            left: 0;
            top: 0;
            background-size: cover;
            z-index: -1;
            opacity: 0;
            transition: all 0.3s ease-in-out;
        }
        &:before {
            left: 0;
            top: 0;
            width: calc(100% - 2px);
            height: calc(100% - 2px);
            position: absolute;
            content: "";
            border: solid 1px #a88969;
            z-index: -1;
        }
        &:hover {
            color: #3f2709;
            font-weight: 700;
            text-shadow: 0 1px 0 rgba(255,255,255,0.4);
            &:after {
                opacity: 1;
            }
        }
        &.is-active {
            color: #3f2709;
            font-weight: 700;
            text-shadow: 0 1px 0 rgba(255,255,255,0.4);
            &:after {
                opacity: 1;
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            height: 80px;
            border: 0;
            font-size: 20px;
            line-height: 80px;
            position: relative;
            color: #a88969;
            &::before {
                border: 0;
                width: 100%;
                height: 2px;
                background: rgb(144,123,93);
                background: linear-gradient(90deg, rgba(144,123,93,0) 0%, rgba(144,123,93,1) 15%, rgba(144,123,93,1) 85%, rgba(144,123,93,0) 100%);
                position: absolute;
                top: 0;
                left: 0;
            }
            &::after {
                border: 0;
                width: 100%;
                height: 2px;
                background: rgb(144,123,93);
                background: linear-gradient(90deg, rgba(144,123,93,0) 0%, rgba(144,123,93,1) 15%, rgba(144,123,93,1) 85%, rgba(144,123,93,0) 100%);
                position: absolute;
                top: inherit;
                bottom: 0;
                left: 0;
                opacity: 0;
            }
            &:last-child {
                &:after {
                    opacity: 1;
                }
            }
            &.is-active {
                color: #fff;
                font-weight: 700;
                text-shadow: none;
                &:after {
                    opacity: 0;
                }
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 18px;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 20px;
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: 20px;
        }
    }
    &__container {
        position: relative;
        box-sizing: border-box;
        width: 1200px;
        margin: 0 auto 32px;
        padding: 0;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            padding: 96px 16px 16px;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 720px;
            padding: 24px;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 800px;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 900px;
        }
    }
    &__btn-back {
        width: 42px;
        height: 42px;
        display: block;
        position: absolute;
        right: 0;
        bottom: -72px;
        img {
            width: 100%;
            display: block;
            margin: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 32px;
            height: 32px;
            bottom: -70px;
            right: 16px;
        }
    }
    &__btn-toggle {
        width: 18px;
        height: 16px;
        position: absolute;
        right: 16px;
        top: 16px;
        border: 0;
        padding: 0;
        background-color: transparent;
        z-index: 50;
        display: none;
        @include susy-breakpoint($s-320, $c8) {
            display: block;
        }
        span {
            width: 18px;
            height: 2px;
            background-color: #957b5e;
            left: 50%;
            transform: translate3d(-50%,-50%,0);
            position: absolute;
            top: 50%;
            transition: all 0.6s ease-in-out;
        }
        &::before {
            content: "";
            width: 18px;
            height: 2px;
            background-color: #957b5e;
            left: 50%;
            transform: translate3d(-50%,-400%,0);
            position: absolute;
            top: 50%;
            transform-origin: 50%;
            transition: all 0.6s ease-in-out;
        }
        &::after {
            content: "";
            width: 18px;
            height: 2px;
            background-color: #957b5e;
            left: 50%;
            transform: translate3d(-50%,290%,0);
            position: absolute;
            top: 50%;
            transform-origin: 50%;
            transition: all 0.6s ease-in-out;
        }
        &.is-active {
            span {
                opacity: 0;
                transform: translate3d(-50%,-50%,0) scaleX(0);
            }
            &::before {
                transform: translate3d(-50%,-50%,0) rotate(45deg);
            }
            &::after {
                transform: translate3d(-50%,-50%,0) rotate(-45deg);
            }
        }
    }
}

.inf-item {
    font-family: 'Noto Serif TC', serif;
    &__section {
        margin-bottom: 24px;
        transform: translate3d(0,-20px,0);
        transition: all 0.6s ease-in-out 0.3s;
        opacity: 0;
        &.is-show {
            transform: translate3d(0,0,0);
            opacity: 1;
        }
    }
    &__title {
        text-align: center;
        margin-bottom: 8px;
        img {
            height: 48px;
        }
        @include susy-breakpoint($s-320, $c8) {
            img {
                height: 26px;
            }
        }
    }
    &__hgroup {
        margin-bottom: 48px;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 16px;
        }
    }
    &__h3 {
        text-align: center;
        color: #cda572;
        font-size: 25px;
        margin-bottom: 12px;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 15px;
        }
    }
    &__h4 {
        font-size: 18px;
        color: #c6a17a;
        line-height: 1.6;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 15px;
            margin-bottom: 4px;
        }
    }
    &__h5 {
        color: #8dbacf;
        text-align: center;
        font-size: 18px;
        line-height: 1.6;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 14px;
        }
    }
    &__rule-list {
        padding: 0;
        margin: 0;
        &--w560 {
            width: 560px;
            margin: 0 auto;
            @include susy-breakpoint($s-320, $c8) {
                width: 100%;
            }
        }
        li {
            position: relative;
            font-size: 18px;
            color: #e7c59d;
            line-height: 1.6;
            margin-bottom: 8px;
            padding-left: 18px;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 14px;
            }
            &:last-child {
                margin-bottom: 0;
            }
            &::before {
                position: absolute;
                top: 14px;
                left: 4px;
                content: "";
                background-color: #b69c82;
                width: 8px;
                height: 1px;
                @include susy-breakpoint($s-320, $c8) {
                    top: 12px
                }
            }
        }
        a {
            color: #ffbd3d;
            transition: color 0.4s ease-in-out;
            &:hover {
                color: #fff;
            }
        }
    }
    &__text {
        position: relative;
        font-size: 18px;
        color: #e7c59d;
        line-height: 1.6;
        text-align: center;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 14px;
        }
    }
}

.dragon-intro {
    font-size: 0;
    &__dragon {
        width: calc(50% - 32px);
        display: inline-block;
        margin: 0 16px 64px;
        img {
            width: 100%;
            display: block;
            margin: 0 0 8px;
        }
        p {
            color: #f9f1e4;
            font-size: 22px;
            text-align: center;
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 15px;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 18px;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 20px;
            }
        }
        &:nth-child(3),
        &:nth-child(4) {
            margin-bottom: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            margin-left: 0;
            margin-right: 0;
            margin-bottom: 44px;
            &:nth-child(3) {
                margin-bottom: 44px;
            }
            &:nth-child(4) {
                margin-bottom: 44px;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            margin-bottom: 24px;
            margin-left: 0;
            margin-right: 0;
            width: 100%;
            margin-bottom: 44px;
            &:nth-child(3) {
                margin-bottom: 44px;
            }
            &:nth-child(4) {
                margin-bottom: 44px;
            }
        }
        @include susy-breakpoint($s-960, $c12) {
            margin-bottom: 32px;
        }
    }
}

.stage-intro {
    position: relative;
    @include susy-breakpoint($s-320, $c8) {
        background: #000 url(../images/inf-map-m.jpg) no-repeat center top;
        background-size: cover;
        padding: 24px 56px;
    }
    &__bg {
        position: relative;
        img {
            width: 100%;
            display: block;
            margin: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            display: none;
        }
    }
    &__stage {
        width: 37%;
        position: absolute;
        &:nth-child(1) {
            left: 8px;
            top: 8px;
            z-index: 0;
        }
        &:nth-child(2) {
            left: 50%;
            transform: translate3d(-50%,0,0);
            bottom: 8px;
            z-index: 10;
        }
        &:nth-child(3) {
            right: 8px;
            top: 8px;
            z-index: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            position: relative;
            &:nth-child(1) {
                left: inherit;
                top: inherit;
                z-index: 0;
            }
            &:nth-child(2) {
                left: inherit;
                transform: translate3d(0,0,0);
                bottom: inherit;
                z-index: 10;
            }
            &:nth-child(3) {
                right: inherit;
                top: inherit;
                z-index: 0;
            }
        }
    }
    &__img {
        width: 100%;
        margin-bottom: 96px;
        position: relative;
        z-index: 10;
        img {
            width: 100%;
            margin: 0;
            display: block;
        }
        &--center {
            margin-bottom: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 12px;
        }
        @include susy-breakpoint($s-768, $c12) {
            margin-bottom: 32px;
            &--center {
                margin-bottom: 0;
            }
        }
        @include susy-breakpoint($s-960, $c12) {
            margin-bottom: 64px;
            &--center {
                margin-bottom: 0;
            }
        }
        @include susy-breakpoint($s-1201, $c18) {
            margin-bottom: 80px;
            &--center {
                margin-bottom: 0;
            }
        }
    }
    &__detail {
        width: 80%;
        text-align: center;
        position: relative;
        z-index: 0;
        &:after {
            width: 16px;
            height: 87px;
            background: transparent url(../images/arrow@2x.png) no-repeat center top;
            position: absolute;
            content: "";
            left: 50%;
            bottom: 0;
            background-size: cover;
        }
        h3 {
            font-size: 25px;
            color: #ffffff;
            line-height: 1.2;
            margin-bottom: 12px;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 15px;
                margin-bottom: 8px;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 16px;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 18px;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 20px;
            }
        }
        P {
            background-color: rgba(123,162,181,0.6);
            display: inline-block;
            text-align: center;
            color: #bbe2f7;
            border-radius: 48px;
            line-height: 30px;
            height: 30px;
            font-size: 18px;
            padding: 0 16px;
            margin-bottom: 6px;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 14px;
                line-height: 22px;
                height: 24px;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 12px;
                line-height: 24px;
                height: 24px;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 13px;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 14px;
            }
        }
        h4 {
            font-size: 18px;
            color: #8dbacf;
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 14px;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 12px;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 13px;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 14px;
            }
        }
        &--left {
            &::after {
                transform: translate3d(-50%,-140%,0);
            }
            @include susy-breakpoint($s-768, $c12) {
                &::after {
                    transform: translate3d(-50%,-95%,0);
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                &::after {
                    transform: translate3d(-50%,-110%,0);
                }
            }
        }
        &--center {
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate3d(-50%,-190%,0);
            &::after {
                transform: translate3d(-50%,120%,0) rotate(180deg);
            }
            @include susy-breakpoint($s-768, $c12) {
                transform: translate3d(-50%,-160%,0);
            }
            @include susy-breakpoint($s-960, $c12) {
                transform: translate3d(-50%,-180%,0);
            }
        }
        &--right {
            margin-right: 0;
            margin-left: auto;
            &::after {
                transform: translate3d(-50%,-140%,0);
            }
            @include susy-breakpoint($s-768, $c12) {
                &::after {
                    transform: translate3d(-50%,-95%,0);
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                &::after {
                    transform: translate3d(-50%,-110%,0);
                }
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            margin-bottom: 40px;
            &--left {
                &::after {
                    display: none;
                }
            }
            &--center {
                position: static;
                top: inherit;
                left: inherit;
                transform: translate3d(0,0,0);
                &::after {
                    display: none;
                }
            }
            &--right {
                margin-right: auto;
                &::after {
                    display: none;
                }
            }
        }
    }
}

.skill-intro {
    margin-bottom: 64px;
    @include susy-breakpoint($s-320, $c8) {
        margin-bottom: 24px;
    }
    &__wrap {
        font-size: 0;
    }
    &__skill {
        cursor: pointer;
        display: inline-block;
        width: calc(33% - 2px);
        border: solid 1px #584d40;
        vertical-align: top;
        margin: 0 1px 2px;
        box-sizing: border-box;
        padding: 24px 8px;
        position: relative;
        min-height: 158px;
        position: relative;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            margin-left: 0;
            margin-right: 0;
            margin-bottom: 6px;
            min-height: inherit;
            padding: 16px 8px;
        }
        @include susy-breakpoint($s-768, $c12) {
            min-height: 198px;
            padding: 16px 4px;
        }
        @include susy-breakpoint($s-960, $c12) {
            min-height: 118px;
            padding: 16px 4px;
        }
        @include susy-breakpoint($s-1201, $c18) {
            min-height: 120px;
            padding: 16px 4px;
        }
        &::after {
            position: absolute;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,0.5) url(../images/icon-play@2x.png) no-repeat center center;
            background-size: 70px 70px;
            content: "";
            z-index: 10;
            opacity: 0;
            transition: opacity 0.6s ease-in-out;
            top: 0;
            left: 0;
        }
        &:hover {
            &::after {
                opacity: 1;
            }
            @include susy-breakpoint($s-320, $c8) {
                &::after {
                    opacity: 0;
                }
            }
        }
    }
    &__skill-img {
        width: 80px;
        display: inline-block;
        vertical-align: top;
        position: relative;
        &::after {
            width: 32px;
            height: 32px;
            background: transparent url(../images/icon-play-m@2x.png) no-repeat center top;
            background-size: 32px;
            position: absolute;
            z-index: 1;
            left: 50%;
            top: 50%;
            transform: translate3d(-50%,-50%,0);
            opacity: 0;
            content: "";
        }
        img {
            max-width: 100%;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 48px;
            margin-right: 8px;
            &::after {
                opacity: 1;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            display: block;
            width: 100%;
            margin: 0 auto;
            text-align: center;
        }
